export default class Media {
    constructor() {
        this.handler();
        this.events();
    }

    handler() {
        if ($(window).width() <= 1280) {
            $('[data-desktop="sub-directory"]').find($('[data-move="sub-directory"]')).appendTo('[data-mobile="sub-directory"]');
            $('[data-mobile="header-link"]').before($('[data-move="header-link"]'));
        }
        else {
            $('[data-mobile="sub-directory"]').find($('[data-move="sub-directory"]')).prependTo('[data-desktop="sub-directory"]');
            $('[data-move="header-link"]').appendTo('[data-desktop="header-link"]');
        }
        if ($(window).width() <= 880) {
            $('[data-desktop="product-title"]').find($('[data-move="product-title"]')).prependTo('[data-mobile="product-title"]');
            $('[data-desktop="product-flags"]').find($('[data-move="product-flags"]')).appendTo('[data-mobile="product-flags"]');
        } 
        else {
            $('[data-mobile="product-title"]').find($('[data-move="product-title"]')).prependTo('[data-desktop="product-title"]');
            $('[data-mobile="product-flags"]').find($('[data-move="product-flags"]')).appendTo('[data-desktop="product-flags"]');
        }
        if ($(window).width() <= 600) {
            $('[data-mobile="stocks"]').after($('[data-move="stocks"]'));
            $('[data-desktop="advantages"]').find($('[data-move="advantages"]')).appendTo('[data-mobile="advantages"]');
            $('[data-desktop="news"]').find($('[data-move="news"]')).appendTo('[data-mobile="news"]');
        }
        else {
            $('[data-move="stocks"]').appendTo('[data-desktop="stocks"]');
            $('[data-mobile="advantages"]').find($('[data-move="advantages"]')).appendTo('[data-desktop="advantages"]');
            $('[data-mobile="news"]').find($('[data-move="news"]')).appendTo('[data-desktop="news"]');
        }
    }

    events() {
        const self = this;

        $(window).on('resize', function () {
            self.handler();
        });
    }
}