export default class MobileCatalog {
    constructor() {
        this.init();
        this.events();
    }

    init() {
        this.moveElementMobileCatalog();
    }

    mobileCatalogInit() {
        if(!$('.catalog').hasClass('mobile')) {
            $('.catalog').addClass('mobile');
            const catalogList = $('.catalog > .catalog__list')[0];
            const mobileCatalogList = $('.catalog__mobile-list')[0];
            const mobileCatalogItem = $('.catalog__mobile-item-list')[0];
            const catalogBtn = $('.catalog__btn')[0];
            mobileCatalogItem.appendChild(catalogBtn);
            mobileCatalogItem.appendChild(catalogList);
            $('.catalog__item-list').each(function () {
                mobileCatalogList.appendChild($(this)[0]);
            })
        } else {
            return;
        }
    }

    desktopCatalogInit() {
        if($('.catalog').hasClass('mobile')) {
            $('.catalog').removeClass('mobile');
            const catalog = $('.catalog')[0];
            const catalogList = $('.catalog__mobile-item-list > .catalog__list')[0];
            const catalogBtn = $('.catalog__mobile-item-list > .catalog__btn')[0];
            const catalogItemLists = $('.catalog__mobile-list > .catalog__item-list');
            catalog.prepend(catalogList);
            catalog.prepend(catalogBtn);
            catalogItemLists.each(function () {
                catalogList.prepend($(this)[0]);
            })
        } else {
            return;
        }
    }

    moveElementMobileCatalog() {
        if ($(window).width() >= 1280) {
            this.desktopCatalogInit();
        }
        else {
            this.mobileCatalogInit();
        }
    }

    events() {
        $(window).on('resize', () => {
            this.moveElementMobileCatalog();
        })
    }
}