export default class MobileSorting {
    sorting = '[data-sorting]';
    openBtn = '[data-sorting-open]';
    closeBtn = '[data-sorting-close]';

    constructor() {
        this.events();
    } 

    openMobileSorting() {
        $('[data-sorting]').addClass('open');
        document.body.style.overflow = 'hidden';
    }

    closeMobileSorting() {
        $('[data-sorting]').removeClass('open');
        document.body.style.overflow = 'auto';
    }

    events() {
        let self = this;

        $('[data-sorting-close]').on('click', function () {
            self.closeMobileSorting();
        });

        $('[data-sorting-open]').on('click', function () {
            if($(this).hasClass('active')) {
                self.closeMobileSorting();
            } else {
                self.openMobileSorting();
            }
        });
    }
}