export default class Tooltip {
    openBtn = '[data-tooltip-open]';
    closeBtn = '[data-tooltip-close]';
    tooltip = '[data-tooltip]';

    constructor() {
        this.events();
    }

    events() {
        let self = this;
        $('[data-tooltip-open]').on('click', function() {
            const tooltipId = $(this).attr('data-tooltip-open');
            const tooltip = $('[data-tooltip='+ tooltipId + ']');
            if(tooltip) {
                self.closeAll();
                self.open(tooltip);
            }
        })
        $('[data-tooltip-close]').on('click', function() {
            const tooltip = $(this).closest('[data-tooltip]');
            if(tooltip) {
                self.close(tooltip);
            }
        })
        $(document).on('click', function(e) {
            const isTooltip = e.target.closest('[data-tooltip]');
            const isOpenTooltip = e.target.closest('[data-tooltip-open]');
            if(!isTooltip && !isOpenTooltip) {
                self.closeAll();
            }
        })
    }

    open(item) {
        item.show();
    }

    close(item) {
        item.hide();
    }

    closeAll() {
        $('[data-tooltip]').each(function() {
            $(this).hide();
        })
    }
}