import { createPopper } from "@popperjs/core";

export default class Popovers { 
    constructor() {
        this.slider = document.querySelector("#sliderIntro");
        this.events();
    }

    events() {
        let self = this;
        if(this.slider) {
            this.slider.addEventListener('click', function(e) {
                const button = e.target.closest('[data-card-id]');
                if(button) {
                    const tooltip = document.querySelector("[data-card='" + button.getAttribute('data-card-id') + "']");
                    console.log(button)
                    const popInstance = self.init(button, tooltip);
            
                    document.addEventListener("click", function (e) {
                        const isCard = e.target.closest(".product-card");
                        const isButton = e.target.closest(".intro__slide-dot");
                        if (!isCard && isButton!==button) {
                            self.destroy(button, tooltip, popInstance);
                        }
                    });
                }
            })
        }
    }
    
    init(button, tooltip) {
        button.classList.add("active");
        tooltip.classList.add("active");
        return createPopper(button, tooltip, {
            placement: "left",
            modifiers: [
            {
                name: "preventOverflow",
                options: {
                altAxis: false,
                boundary: tooltip.parentElement,
                padding: 8,
                },
            },
            {
                name: "offset",
                options: {
                offset: [0, -17],
                },
            },
            ],
        });
    }

    destroy(button, tooltip, popInstance) {
        button.classList.remove("active");
        tooltip.classList.remove("active");
        popInstance.destroy();
    }
}