export default class Order {
    checkbox = '[data-switch-checkbox]';
    switchingBlock = '[data-switching-block]';

    constructor() {
        this.events();
    }

    events() {
        let self = this;
        $('[data-switch-checkbox]').on('change', function(e) {
            if(this.checked === true) {
                self.closeBlocks($('[data-switching-block]'));
            } else {
                self.openBlocks($('[data-switching-block]'));
            }
        })
    }

    closeBlocks(item) {
        item.find('input').val('');
        item.hide();
    }

    openBlocks(item) {
        item.show();
    }
}