export default class TextCropping {
    selector = '[data-text-cropping]';
    maxLengthAttr = 'data-text-cropping';
    textMoreTogglerAttr = 'data-text-cropping-more-btn';

    /** @private */
    togglerAttr = 'data-text-toggler';    

    constructor() {
        const self = this;
        const elems = document.body.querySelectorAll(this.selector);
        this.nodes = [].map.call(elems, function(elem) {
            const maxLength = parseInt(elem.getAttribute(self.maxLengthAttr));
            const togglerText = elem.getAttribute(self.textMoreTogglerAttr) || '';
            const hasToggler = !!togglerText;
            return {
                elem: elem,
                initialText: elem.textContent,
                togglerText: togglerText,
                hasToggler: hasToggler,
                maxLength: maxLength
            }
        });
        
        this.onMoreBtnClick = function(e) {
            if (!e.target.getAttribute(self.togglerAttr)) {
                return;
            }

            const parent = e.target.parentElement;
            const node = self.nodes.find(function(node) {
                node.elem === parent;
            });

            if (!node) {
                return;
            }

            node.elem.textContent = node.initialText;
        }

        document.body.addEventListener('click', this.onMoreBtnClick);
        this.init();
    }

    init() {
        const self = this;
        this.nodes.forEach(function (node) {
            node.elem.setAttribute('title', node.initialText);
            node.elem.innerHTML = self.trimString(
                node.initialText, 
                node.maxLength, 
                node.togglerText
            )
        });
    }

    destroy() {
        document.body.removeEventListener('click', this.onMoreBtnClick)
    }

    trimString(source, maxLength, togglerText) {
        if (source.length <= maxLength) {
            return source;
        }

        
        const togglerExist = !!togglerText;
        const spread = '... ';
        const spreadLength = spread.length + (togglerText || '').length;
        const diff = maxLength - spreadLength;
        const shouldSliceTo = diff > 0 ? diff : 0;
        const trimed = source.slice(0, shouldSliceTo);

        return togglerExist ?
            trimed + spread + this.buildToggler(togglerText) :
            trimed + spread;
    }

    buildToggler(inner) {
        return '<span ' + this.togglerAttr +'="true">' + inner + '</span>';
    }
}