export default class Catalog {
    constructor() {
        this.events();
    }

    events() {
        let self = this;

        $('.catalog > .catalog__btn').on('click', function () {
            if(!$('.catalog > .catalog__list').hasClass('open')) {
                $('.catalog > .catalog__list').addClass('open');
            } else {
                self.closeAll();
            }
        })

        $('[data-catalog-toggler]').on('click', function (e) {
            const toggleBtn = e.currentTarget;
            if(toggleBtn.closest('[data-catalog-item]')) {
                const catalogList = toggleBtn.closest('[data-catalog-item]').querySelector('.catalog__list');
                self.toggle(toggleBtn, catalogList);
            }
        });

        $(document).on('click', function (e) {
            const isCatalog = e.target.closest('.catalog');
            if(!isCatalog) {
                self.closeAll();
            }
        }); 

        $('.mobile-back-btn').on('click', function() {
            self.closeNestedLists();
        })
    }

    mobileCatalogOpen(btnText) {
        $('.mobile-back-btn').addClass('active');
        $('.mobile-back-btn span').text(btnText);
    }

    mobileCatalogClose() {
        $('.mobile-back-btn').removeClass('active');
        $('.catalog__btn').removeClass('active');
    }

    toggle(button, list) {
        this.closeNestedLists();

        $(button).toggleClass('active');
        $(list).toggleClass('open');

        if($('.catalog').hasClass('mobile')) {
            const btnText = $(button).text();
            this.mobileCatalogOpen(btnText);
        }
    }

    closeNestedLists() {
        $('ul .catalog__list').each(function() {
            $(this).removeClass('open');
        })
        $('ul .catalog__link').each(function() {
            $(this).removeClass('active');
        })
        this.mobileCatalogClose();
    }

    closeAll() {
        $('.catalog__list').each(function() {
            $(this).removeClass('open');
        })
        $('.catalog__link').each(function() {
            $(this).removeClass('active');
        })
        this.mobileCatalogClose();
    }
}
