export default class Sorting {
    select = '[data-sorting]';
    input = '[data-sorting-input]';
    optionResult = '[data-sorting-value]';
    placeholder = '[data-sorting-placeholder]';
    option = '[data-sorting-dropdown-option]';

    constructor() {
        this.events();
    }

    dropdownShow($this) {
        $this.toggleClass('is-show');
    }

    handler($this, e) {
        const
            $container = $this.closest($(this.select)),
            containerValue = $container.data('sorting'),
            $input = $container.find($(this.input)),
            $placeholder = $container.find($(this.placeholder)),
            val = $this.attr('data-sorting-dropdown-option');

        if (containerValue === 'set-input') {
            e.preventDefault();
            $input.val(val);
            $placeholder.hide()
        }

        $this
            .closest('.sorting-dropdown-list__item')
            .addClass('current')
            .siblings()
            .removeClass('current');
        $(this.optionResult).text(val);
    }

    selectClose(e) {
        let target = e.target;
        let $select = $(this.select);

        if ($select.hasClass('is-show') && !$select.is(target) && $select.has(target).length === 0) {
            $select.removeClass('is-show');
            e.stopPropagation();
        }
    }

    events() {
        const self = this;

        $(document).on('click', this.select, function (e) {
            // e.preventDefault();
            self.dropdownShow($(this), e);
        });
        $(document).on('click', this.option, function (e) {
            self.handler($(this), e);
        });
        $(document).on('click', function(e) {
            self.selectClose(e);
        });
    }
}