export default class CatalogPage {
    modeBtn = '[data-catalog-mode]';
    modeItem = '[data-catalog-item]';
    moveElement = '[data-item-move]';
    horisontalInnerForElem = '[data-item-horisontal]';
    verticalInnerForElem = '[data-item-vertical]';

    constructor() {
        this.events();
    }

    events() {
        let self = this;
        $('[data-catalog-mode]').on('click', function() {
            $('[data-catalog-mode]').removeClass('active');
            if($(this).attr('data-catalog-mode') == 'horisontal') {
                self.horisontalMode();
            } else {
                self.verticalMode();
            }
        })
    }

    horisontalMode() {
        $('[data-catalog-item]').addClass('product-card--horisontal');
        $('[data-catalog-mode="horisontal"]').addClass('active');

        $('[data-item-move]').each(function() {
            $(this.closest('[data-catalog-item]')).find('[data-item-horisontal]').prepend(this);
        })
    }

    verticalMode() {
        $('[data-catalog-item]').removeClass('product-card--horisontal');
        $('[data-catalog-mode="vertical"]').addClass('active');

        $('[data-item-move]').each(function() {
            $(this.closest('[data-catalog-item]')).find($('[data-item-vertical]')).append(this);
        })
    }
}