import "@fancyapps/fancybox";
import "@fancyapps/fancybox/dist/jquery.fancybox.min.css";

export default class Modals {
  modals = [
    {
      selector: "[data-modal-open]",
      options: {
        type: "inline",
        baseClass: "fancybox-modal",
      },
    },
  ];

  constructor() {
    this.events();
    this.init();
  }

  init() {
    $("[data-fancybox]").fancybox({
      buttons: ["close"],
      baseClass: "fancybox-modal-img",
      loop: true,
      btnTpl: {
        close:
          '<button data-fancybox-close class="fancybox-button fancybox-close-small">' +
          '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">' +
          '<path d="M13 12l5-5-1-1-5 5-5-5-1 1 5 5-5 5 1 1 5-5 5 5 1-1z"/>' +
          "</svg>" +
          "</button>",

        arrowLeft:
          '<button data-fancybox-prev class="slider__prev swiper-button-prev">' +
          '<svg class="i-icon">' +
          '<use xlink:href="#icon-arrow"></use>' +
          "</svg>" +
          "</button>",

        arrowRight:
          '<button data-fancybox-next class="slider__next swiper-button-next">' +
          '<svg class="i-icon">' +
          '<use xlink:href="#icon-arrow"></use>' +
          "</svg>" +
          "</button>",
      },
      thumbs: {
        autoStart: true,
        parentEl: ".fancybox-inner",
      },
      helpers: {
        title: {
          type: "outside",
        },
      },
      // beforeShow:  function(instance) {
      //    instance.$refs.infobar.text('').append('<span data-fancybox-index></span> из <span data-fancybox-count></span> ')
      //     if (Object.keys(instance.slides).length < 2) {
      //         console.log($('.fancybox-container'));
      //         $('.fancybox-container').addClass('nav-hide');
      //     }
      //     else {
      //         $('.fancybox-container').removeClass('nav-hide');
      //     }
      // }
    });

    $("[data-fancybox-second]").fancybox({
      buttons: ["close"],
      baseClass: "fancybox-modal-second",
      btnTpl: {
        close:
          '<button data-fancybox-close class="fancybox-button fancybox-close-small">' +
          '<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">' +
          '<path d="M13 12l5-5-1-1-5 5-5-5-1 1 5 5-5 5 1 1 5-5 5 5 1-1z"/>' +
          "</svg>" +
          "</button>",
      },
    });
  }

  openModal(href) {
    $.fancybox.close();
    $.fancybox.open({
      src: href,
      type: "inline",
      opts: {
        beforeClose: this.closeModalForms,
        baseClass: "fancybox-modal",
        buttons: [],
      },
    });
  }

  closeModalForms() {
    const modal = this.$slide;
    modal.find($("[data-form-submit]")).attr("disabled", true);
    const inputs = modal.find($("input"));
    inputs.each(function () {
      App.Forms.FormValidation.valid($(this));
      this.checked = false;
      $(this).value = '';
    });
  }

  events() {
    let self = this;

    $(document).on("click", "[data-modal-open]", function (e) {
      e.preventDefault();
      self.openModal($(this).attr("href"));
    });
  }
}
