export default class MobileMenu {
    constructor() {
        this.mobileBtn = document.body.querySelector('.mobile-btn');
        this.mobileInner = document.body.querySelector('.mobile-inner')
        this.mobileMenu = document.body.querySelector('.mobile-menu');
        this.mobileBackBtn = document.body.querySelector('.mobile-back-btn');
        this.header = document.body.querySelector('.header__bottom');
        this.overlay = document.body.querySelector('.overlay');
        this.events();
    }

    events() {
        let self = this;
        this.mobileBtn.addEventListener('click', function() {
            if(self.mobileMenu.classList.contains('active')) {
                self.close();
            } else {
                self.open();
            }
        })
        this.overlay.addEventListener('click', function() {
            self.close();
        })
    }
    open() {
        this.mobileBtn.classList.add('active');
        this.mobileMenu.classList.add('active');
        this.mobileInner.classList.add('active');
        document.body.style.overflow = 'hidden';
        this.header.classList.add('open');
        this.overlay.style.display = 'block';
    }

    close() {
        this.mobileBtn.classList.remove('active');
        this.mobileMenu.classList.remove('active');
        this.mobileBackBtn.classList.remove('active');
        this.mobileInner.classList.remove('active');
        document.body.style.overflow = 'auto';
        this.header.classList.remove('open');
        this.overlay.style.display = 'none';
    }
} 